.hotline-widget {
  position: fixed;
  z-index: 1000;
  right: 200px;
  border: 1px solid #ccc;
  background-color: #fff;
  box-shadow: 0px 0px 6px 1px #adbcc4;
}
.hotline-widget .sdk-container .next-loading .next-loading-component .next-icon-boda {
  display: none;
}
.next-balloon-normal {
  display: none;
}
.ant-modal-title {
  font-size: 16px;
}
.modal-form .ant-modal-body .ant-form-item {
  display: flex;
  width: 90%;
}
.modal-form .ant-modal-body .ant-form-item .ant-form-item-label {
  width: 140px;
}
.modal-form .ant-modal-body .ant-form-item .ant-form-item-control-wrapper {
  width: 100%;
}
.modal-form .ant-modal-body .ant-form-item .ant-input-number {
  width: 130px;
}
.login-modal {
  z-index: 2000;
}
.login-modal .login-container .ant-form-item-control {
  height: 50px;
}
.login-modal .login-container .register-area {
  text-align: right;
}
.login-modal .login-container .register-area span {
  color: orange;
}
